.banner{
 margin-top:- 10vh;
 height: 80VH;
 margin-bottom: -80px;
 color: white;
 object-fit: contain;
}



.banner_contents{
    margin-left: 30px;
    margin-top: 30px;
    padding-top: 140px;
    color:white;
}
.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner_description{
    width: 45rem;
    line-height: 1;
    padding: 1rem 0;
    font-size: 1rem;
    max-width: 360px;
    height: 80px;
}
.banner__button {
	cursor: pointer;
	color: #fff;
	outline: none;
	border: none;
	font-weight: 700;
	border-radius: 0.2vw;
	padding-left: 2rem;
	padding-right: 2rem;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	margin-right: 1rem;
	background-color: rgba(51, 51, 51, 0.5);
}
.banner__button:hover {
	color: #000;
	background-color: #c6c6c6;
	transition: all0.2s;
}
.play{
    color: #000;
    background-color: #fff;
   

}

.banner_fadebottom{
    height: 40vh;
    background-image: linear-gradient(
        180deg,transparent,rgb(37,37,37,0.91),#111
    );
}
